.container {
  padding: 30px;
}

.table {
  width: 70%;
  margin: 0 auto;
}
td {
  width: 250px;
  border: 1px solid #ddd;
}
tr {
  height: 50px;
}
img {
  height: 40px;
  width: 40px;
  border: 1.5px solid black;
  border-radius: 5px;
}
.position {
  width: 50px;
  font-weight: bold;
}
.centralized, .titles, .header {
  text-align: center;
}
.titles {
  background-color: #ddd;
}
.profile {
  margin: 10px;
  font-size: 20px;
}
.header {
  font-family: cursive;
}



